@import "https://cdn.jsdelivr.net/npm/bulma@0.9.2/css/bulma.min.css";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.index-container {
  color: white;
  padding: 20px;
}

h1 {
  font-size: 32px;
  margin-bottom: 5px;
}

h2 {
  font-size: 26px;
  margin-bottom: 10px;
  border-bottom: solid;
}

h3 {
  font-size: 20px;
}

p {
  margin-bottom: 5px;
}

img {
  border: solid;
}

button:hover {
  background-image: linear-gradient(rgba(0, 0, 0, 0.1) 0 0);
}

.character {
  text-align: center;
}

.inactive-character-tile {
  color: white;
}

.selected-character-tile {
  color: #3273DC;
}

.hover-tile:hover {
  color: #19B096;
}

/* .column-nest ORIGINAL BACKGROUND COLOR AND BOX SHADOW */
/* background-color: #1FD1B2; */
/* box-shadow: 12px 12px 2px 1px rgba(72, 199, 142, .2); */
.column-nest {
  background-color: #8BDFF9;
  border-radius: 2%;
  box-shadow: 12px 12px 2px 1px rgba(12, 170, 220, .2);
  height: 75%;
}

/* bulma is-danger(red) color #F14668 */
.rival-nest {
  background-color: #F14668;
  border-radius: 2%;
  box-shadow: 12px 12px 2px 1px rgba(176, 46, 12, .2);
  /* original rival nest box shadow */
  /* box-shadow: 12px 12px 2px 1px rgba(12, 170, 220, .2); */
  height: 75%;
}

.player-nest {
  background-color: #0CAADC;
  border-radius: 2%;
  box-shadow: 12px 12px 2px 1px rgba(12, 170, 220, .2);
  height: 75%;
}
